.cartPage{
    width: 55%;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px auto 0;
    margin-bottom: 50px;
}

.cartPage .cartHeader{
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;
}

.cartPage .cartHeader .headerBlock {
    text-transform: capitalize;
    width: 23%;

}

.cartPage .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
}